}

.Einkaufsliste{
  height: auto;
  width: 90vw;
  background-color: darkslateblue;
  margin: 8% auto 2%;
  border: solid black 5px;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  padding-bottom: 3%;
}

.Eingabekonsole{
  width: 50vw;
  height: auto;
  background-color:darkslateblue;
  border: solid, black, 5px;
  border-radius: 15px;
  margin: auto;
}

#Produkttabelle{
  font-size: 3vw;
  margin: 4% 10% auto;
}
#Laden{
  width: 100%;
  font-size: 90%;
}

input{
