.Produkt{
   height: 10vw;
  width: 80vw;
  margin: 4% auto 0px auto;
  border: solid black 5px;
  border-radius: 15px;
   display: flex;
  align-items: center; 
  justify-content: flex-start; 
  background-color: lightgray;
  }

  .ProduktAnzahl{
    
    margin-left: 20%;
    height: 10vw;
    width: 30%;
    border-radius: 6px;
    font-size:100%;
  }

  .Nummer{
     padding-left: 2%;
     font-weight: bold;
     font-family: Arial, Helvetica, sans-serif;
     font-size: 90%;
  }

  .Details {
    padding-left: 1%;
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
   font-family: Arial, Helvetica, sans-serif;
     font-size: 90%;
}

